.Works{
    display: inline-block;
    width: 350px;
    /* text-align: center; */
    height: auto;
    position: relative;
    margin: 10px;
}

.container{
    width: 100%;
    height: 250px;
    background-color: rgb(153, 153, 153, 0.3);
    opacity: 0;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: 1s;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container:hover {
    opacity: 1;
    box-shadow: 0 10px 20px rgb(3, 3, 3, 0.6);
}

img {
    width: 100%;
    height: 250px;
    size: auto;
}

h3 {
    color: rgb(248, 34, 34)
}
